body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Magistral", sans-serif;
  background-color: #ffffff;
  color: #021f29;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #ffffff;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.d-none-desktop {
  display: none;
}
@media only screen and (max-width: 991px) {
  .d-none-desktop {
    display: block;
  }
}

.d-none-mobile {
  display: block;
}
@media only screen and (max-width: 991px) {
  .d-none-mobile {
    display: none;
  }
}

.d-none-tablet {
  display: none;
}
@media only screen and (max-width: 767px) {
  .d-none-tablet {
    display: block;
  }
}

.d-none-mobile-tablet {
  display: block;
}
@media only screen and (max-width: 767px) {
  .d-none-mobile-tablet {
    display: none;
  }
}

[hidden] {
  display: none !important;
}

/* Swiper */
.swiper-nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .swiper-nav {
    display: none;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  cursor: pointer;
  pointer-events: auto;
}
.swiper-nav .swiper-btn-prev:hover svg,
.swiper-nav .swiper-btn-next:hover svg {
  color: rgba(255, 255, 255, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transition: 0.3s ease;
}
.swiper-nav--big .swiper-btn-prev svg,
.swiper-nav--big .swiper-btn-next svg {
  width: 30px;
  height: 58px;
}
@media only screen and (max-width: 1279px) {
  .swiper-nav--big .swiper-btn-prev svg,
  .swiper-nav--big .swiper-btn-next svg {
    width: 25px;
    height: 48px;
  }
}
.swiper-nav--outside .swiper-btn-prev {
  left: -50px;
}
@media only screen and (max-width: 1279px) {
  .swiper-nav--outside .swiper-btn-prev {
    left: -34px;
  }
}
.swiper-nav--outside .swiper-btn-next {
  right: -50px;
}
@media only screen and (max-width: 1279px) {
  .swiper-nav--outside .swiper-btn-next {
    right: -34px;
  }
}

.swiper-pagination {
  display: none;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
@media only screen and (max-width: 767px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
  transition: 0.3s ease;
}
.swiper-pagination .swiper-pagination-bullet:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
.swiper-pagination .swiper-pagination-bullet-active {
  width: 14px;
  height: 14px;
}
.swiper-pagination--black .swiper-pagination-bullet {
  background-color: #021f29;
}
.swiper-pagination--black .swiper-pagination-bullet:hover {
  background-color: rgba(2, 31, 41, 0.7);
}

/* Blocks */
.container {
  max-width: 1080px;
  width: 100%;
  padding: 0 40px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.button {
