  align-items: center;
  justify-content: center;
  max-width: 243px;
  width: 100%;
  padding: 14px;
  border-radius: 3px;
  background-color: #ffffff;
  color: #30c2c6;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
